import request from '../utils/https'

// 會員註冊
export function sendMessage (params) {
    return request({
        url: '/message/send',
        method: 'post',
        data:params
    })
}

// 會員登入
export function getMessagList (params) {
    return request({
        url: '/exchange/messageList',
        method: 'post',
        data:params
    })
}

// 會員登出
export function getMessageCount (params) {
    return request({
        url: '/message/count',
        method: 'post',
        data:params
    })
}