</template>

<script>
import exchangeDetail from './exchangeDetail.vue'
import exchangeMessage from './exchangeMessage.vue'

export default {
	name:'exchangeItem',
	components:{
        exchangeDetail,
		exchangeMessage
    },
	props:{
		itemList:{
			type: Array,
			default: []
		},
		isApply:{
			type: Boolean,
			default: false,
		},
		isList:{
			type:Boolean,
			default:true
		}
	},
	data(){
		return {
			defaultimg:require('../../../assets/card1.jpg'),
			curExchangeItem:"",
			showExchangeDetail: false,
			showExchangeMessage: false,
		}
	},
	methods:{
		showDetail(item){
			if (this.isApply){
				this.curExchangeItem = item.exchangeInfo
			} else {
				this.curExchangeItem = item
			}
			if (this.isList){
				this.showExchangeDetail = true
			} else {
				console.log(14)
				this.showExchangeMessage = true
			}
			
		},
		success(){
			this.$emit('success')
		}
	}

}
</script>

