        font-size: 16px;
        font-weight: 600;
      }
      .el-form-item {
        margin-bottom: 0px;
      }
    }
  }
  .check-form {
    font-size: 14px;
      p {
        margin-bottom: 10px;
      }
      .form-title {
        display: inline-block;
